/********** 3. HEADER PANEL **********/
/*************************************/

.headerbar {
  background: #fff;
  top: 0;
  left: 240px;
  right: 0;
  border-bottom: 1px solid rgba(#000, .1);

  .logo {
    display: none;
    img {
      height: 32px;
    }
  }

  .nav-link {
    padding: 0 16px !important;
    height: 50px;
    display: flex;
  }
  .user {
    &-avatar {
      width: 32px;
      height: 32px;
    }
    &-name {
      font-weight: $font-weight-medium;
    }
    &-group {
      line-height: 1em;
      font-size: 12px;
    }
  }
  .noti-item {
    .dropdown-toggle::after {
      display: none;
    }
    .badge {
      bottom: 50%;
      left: 55%;
    }
  }
}


