.dropdown-list li {
  padding:15px;
  overflow:hidden;
  border-bottom:1px solid #eeeeee;
  .badge {
    float:right;
  }
}

.dropdown-list li:last-child {
  border-bottom:0;
  padding:7px 5px;
}

.dropdown-list .thumb {
  width:36px;
  float:left;
  img {
    width:100%;
    display:block;
    vertical-align:middle;
  }
}

.dropdown-list .desc {
  margin-left:45px;
  display:block;
  h5 {
    font-size:13px;
    margin-top:7px;
  }
}

.user-list {
  width:300px;
}

.gen-list li a {
  display:block;
}

.gen-list li a:hover {
  color:#333333 !important;
  text-decoration:none;
}

.gen-list li .name {
  font-size:13px;
  font-weight: $font-weight-bold;
  line-height:21px;
  display:block;
}

.gen-list li .msg {
  font-size:11px;
  line-height:normal;
  color:#999999;
  display:block;
}